/* =========navbar css================= */

.header {
  position: fixed;
  height: 90px;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 9999;
  transition: 0.3s ease-in;
  overflow: hidden;
  background-color: transparent;
}

.activeH {
  background-color: black;
}

.header .navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1240px;
  margin: auto;
  height: 100%;
  padding: 0 1rem;
}

.header .nav-menu a {
  font-size: 14px;
  color: #ffffff;
  text-decoration: none;
  font-family: "Roboto Slab", serif;
}

.header .navbar img {
  display: block;
  max-width: 100%;
  width: 150px;
  height: auto;
  object-fit: cover;
  margin: auto;
}

.header .nav-menu {
  display: flex;
}

.header .nav-item {
  padding: 1rem;
  font-weight: 500;
}

.header .nav-item a:hover {
  padding-bottom: 12px;
  border-bottom: 3px solid white;
}

.hamburger {
  display: none;
}

.hamburger i {
  color: white;
  font-size: 25px;
  font-weight: bold;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

.btn-primary {
  font-family: "Roboto Slab", serif;
  background-color: #0e66f9;
  font-size: 10px;
  padding: 6px 12px;
  border-radius: 5px;
}

@media screen and (max-width: 940px) {
  .header {
    max-width: 100%;
    background-color: black;
  }

  .header .navbar {
    max-width: 100%;
  }

  .hamburger {
    display: block;
  }

  .nav-menu {
    position: fixed;
    left: -100%;
    top: 90px;
    flex-direction: column;
    background-color: black;
    width: 100%;
    height: 100vh;
    z-index: 999;
    text-align: center;
    transition: 0.3s;
  }

  .nav-menu.active {
    left: 0;
  }


  .header .navbar img {
    width: 150px;
  }
}

